@import './reset.scss';

$메인칼라 : #ff0000;

.wow-enter{ //시작할 때
opacity : 0;
}

.wow-enter-active { //동작 때
opacity : 1;
transition: all 500ms;
}


.red{
    color: $메인칼라;
}

.my-alert{
    background: #eeeeee;
    padding: 20px;
    border-radius : 5px;
    max-width: 500px;
    width:100%;
    margin:auto;
}

.my-alert2{
    @extend .my-alert;
    background: #ffe591;
}

.my-alert p{
    margin-bottom: 0;
}